.App
{
    width: 410px;
}

#playerInfo,.scoreCard
{
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0,0,0,.125);
}

.clickButtons
{
    display: inline-block;
    margin-left: 50px;
}

.btn-points
{
    margin-left: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
}

#playerName
{
    padding: .5rem 1rem;
    margin-bottom: 0;
    background-color: rgba(0,0,0,.03);
    border-bottom: 1px solid rgba(0,0,0,.125);
    padding-top: 1rem!important;
    padding-bottom: 1rem!important;
    text-align: center;
}

#playerPoints
{
    margin-bottom: .5rem;
    text-align: center;
}

.createBoard
{
    margin-top: 10px;
    margin-left: 130px;
    margin-bottom: 10px;
}

.addPlayer
{
    margin-left: 10px;
}

h1
{
    display: block;
    margin-block-start: 0.67em;
    margin-block-end: 0.67em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}

.btn
{
    display: inline-block;
    font-weight: 400;
    line-height: 1.5;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    border: 1px solid transparent;
}

.btn-add-player,.btn-search-player
{
    margin-left: 20px;
    margin-bottom: 10px;
}

.btn-search-player{
    margin-top: 10px;
}

.player-name
{
    margin-left: 20px;
}

.iconTrash
{
    margin-left: 50px;
    display: inline-block;
}
.iconClock
{
    display: inline-block;
    margin-left: 10px;
}

#playerCard
{
    display: inline-block;
}

.timer{
    margin : 3rem 0;
    width: 100%;
    display: flex;
    height: 12%;
    justify-content: center;
    align-items: center;
}
  
.digits{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 3rem;
}

.btn-grp
{
    margin-left: 100px;
}

.btn-one
{
    margin-right: 50px;
}

.btn-start
{
    margin-left: 150px;
}

.btn-time
{
    margin-left: 0px;
}

.profileImg
{
    border-radius: 50%;
    margin-right: 10px;
}